import { DatePipe } from '@angular/common';
import { ChangeDetectorRef, Component, OnInit, TemplateRef } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQuerySales } from 'ionic-vhframeworks';
import { NzModalService } from 'ng-zorro-antd/modal';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';
import { differenceInCalendarDays } from 'date-fns';
@Component({
  selector: 'app-control-diary',
  templateUrl: './control-diary.component.html',
  styleUrls: ['./control-diary.component.scss']
})
export class ControlDiaryComponent implements OnInit {
  searchValue: any;
  salesOrder: Array<any> = []
  salesSubOrder: Array<any> = []
  heightScroll: any;
  widthScroll: any;
  load_status: boolean = false;
  dataRestore: any;
  setHeight = { "max-height": "400px", "height": "auto" }
  bill_selected: any = {}
  public date: Date[] = [new Date(), new Date()];
  infoShop: any = {}
  constructor(
    private router: Router,
    public lang: LanguageService,
    public vhComponent: VhComponent,
    public vhAlgorithm: VhAlgorithm,
    private vhQuerySales: VhQuerySales,
    private cdRef: ChangeDetectorRef,
    private vhAuth: VhAuth,
    private modal: NzModalService,
    private datePipe: DatePipe,
  ) {
    this.dataRestore = this.router.getCurrentNavigation().extras.state.dataRestore
  }

  ngOnInit(): void {
    this.infoShop = this.vhQuerySales.getlocalShopListTiki()[0];
  }
  search(value: string) {
    if (value.length) {
      let val: string = this.vhAlgorithm.changeAlias(value.trim().toLowerCase())
      this.salesOrder = this.vhAlgorithm.searchList(val, this.salesSubOrder, ['order_number'])
    } else this.salesOrder = [...this.salesSubOrder]
  }
  goBack() {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/tiki/control/return-control'], {
      state: {
        dataRestore: this.dataRestore
      }
    })
  }
  changeTime(event) {
    if (event[1].getTime() - event[0].getTime() <= 2678400000) {
    } else {
      this.vhComponent.showToast(2000, this.lang.translate("The time to view no more than 30 days"), "alert-toast")
      this.date = [event[0], event[0].getTime() + 2678400000]
    }
  }
  showReport() {
    this.load_status = true;
    this.salesOrder = []
    this.salesSubOrder = []
    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      this.vhQuerySales.getSalesOrderListTiki(new Date(this.date[0].setHours(0, 0, 0)).toISOString(), new Date(this.date[1].setHours(23, 59, 59, 59)).toISOString(), this.infoShop.access_token, this.infoShop._id)
        .then((result: any) => {
          
          this.vhComponent.hideLoading(0)
          result.forEach(item => {
            if (item.status == 'canceled' && (item.main_substate == 'returned_to_seller' || item.cancel_info?.comment == 'Đã hoàn hàng về seller sau khi giao hàng không thành công')) {
              this.salesOrder.push({
                ...item,
                id_shop: this.infoShop._id,
                checked: item.verify_status_received_goods ? 'has checked' : 'Chưa đối soát',
                employee_name: (item.id_verifier_received_goods ? this.vhAuth.getlocalEmployee(item.id_verifier_received_goods).name : ''),
              })
            }
          })
          
          this.salesSubOrder = this.salesOrder
        }).catch(err => { })
    })
  }
  ngAfterViewChecked() {
    if (document.querySelector("#cashflow-management-sendo") && document.querySelector(".ant-table-thead") && document.querySelector(".cashflow-management-sendo-header")) {
      this.heightScroll = document.querySelector("#cashflow-management-sendo").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".cashflow-management-sendo-header").clientHeight - 96 + "px";
    }
    if (document.getElementById("cashflow-management-sendo"))
      this.widthScroll = document.getElementById("cashflow-management-sendo").clientWidth + "px"
    this.cdRef.detectChanges();
  }

  changeShop(event) { }
  /**Render date theo định dạng ngày/tháng/năm */
  format_date(date) {
    return this.datePipe.transform(date, "dd/MM/yyyy")
  }
  //disable date trước 6 tháng, 15552000000 = 60*60*24*1000*30*6
  disabledDate = (current: Date) => {
    return differenceInCalendarDays(current, new Date(new Date().getTime() - 15552000000)) < 0;
  }
  isSort: boolean = false
  sort() {
    this.isSort = !this.isSort
    if (this.isSort) this.salesOrder = [...this.vhAlgorithm.sortStringbyASC(this.salesSubOrder, 'checked')]
    else this.salesOrder = [...this.vhAlgorithm.sortStringbyDESC(this.salesSubOrder, 'checked')]
  }
}
