import { DatePipe } from '@angular/common';
import { ChangeDetectorRef, Component, OnInit, TemplateRef, HostListener } from '@angular/core';
import { Router } from '@angular/router';
import { VhQuerySales, VhAlgorithm, VhQuery, VhAuth } from 'ionic-vhframeworks';
import { NzModalService } from 'ng-zorro-antd/modal';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';
@Component({
  selector: 'app-control-money',
  templateUrl: './return-control-content.component.html',
  styleUrls: ['./return-control-content.component.scss']
})
export class ReturnControlContentComponent implements OnInit {
  nameFile: string = "";
  listData: any[] = [];
  dataRestore: any;
  heightScroll: any;
  listOrderCode: any = [];
  bill_selected: any = {};
  // totalRevenue = 0;
  isUpdateManual = false
  infoShop: any = {}
  constructor(
    private vhQuerySales: VhQuerySales,
    public vhAlgorithm: VhAlgorithm,
    public vhComponent: VhComponent,
    private router: Router,
    private languageService: LanguageService,
    private nzModalService: NzModalService,
    private cdRef: ChangeDetectorRef,
    private vhQuery: VhQuery,
    private vhAuth: VhAuth,
    private modal: NzModalService,
    private datePipe: DatePipe,
  ) {
    this.dataRestore = this.router.getCurrentNavigation().extras.state.dataRestore;
  }

  ngOnInit(): void {
    this.listOrderCode = [];
    this.infoShop = this.vhQuerySales.getlocalShopListTiki()[0];
  }

  ngAfterViewChecked() {
    if (document.querySelector("#control-automation") && document.querySelector(".ant-table-thead") && document.querySelector("#control-automation-header") && document.querySelector(".ant-table-pagination")) {
      this.heightScroll = document.querySelector("#control-automation").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector("#control-automation-header").clientHeight - 90 + "px";
    }
    this.cdRef.detectChanges();
  }

  search(value: string) {
    if (value.length) {
      this.isUpdateManual = true
      // this.totalRevenue = 0;
      // this.vhQuerySales.Tiki
      this.vhComponent.showLoading('').then(() => {
        Promise.all([
          this.vhQuerySales.getSalesOrderTiki_fromViethas([value], this.infoShop._id),
          this.vhQuerySales.getSalesOrderDetailTiki(value, this.infoShop.access_token, this.infoShop._id)
        ])
          .then((array: any) => {
            this.vhComponent.hideLoading(0);
            if (!array[1]) this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Not found"))
            else {
              if (this.listData.findIndex(item => item.bill_code === value) == -1) {
                this.listData = this.listData.concat([{
                  ...array[1], ...array[0][0],
                  bill_code: value,
                  name_control_goods: array[1].verify_status_received_goods ? `${this.vhAuth.getlocalEmployee(array[1].id_verifier_received_goods).name} ${this.languageService.translate("has checked")} ${this.format_date(array[1].verify_date_received_goods)}` : null
                }]);
                if (!array[1].verify_status_received_goods) this.listOrderCode.push(value)
              }
            }
            

          }, error => {
            this.vhComponent.hideLoading(0);
          })
      })
    }
    else this.listData = [];
  }

  /**Render date theo định dạng ngày/tháng/năm */
  format_date(date) {
    return this.datePipe.transform(date, "dd/MM/yyyy HH:mm")
  }

  /**
  * hàm này để cập nhật đơn hàng lưu vào database của mình
  * @param bill 
  * @param data 
  */
  updateRefund(bill, updateForControl: TemplateRef<{}>) {
    if (bill['verify_status_received_goods']) { // nếu update từ false trang true
      this.bill_selected = bill; // gán bill
      this.bill_selected['date_control'] = new Date().toISOString(); // gán ngày update là ngày hôm nay
      this.modal.confirm({ // show modal xác nhận
        nzTitle: this.languageService.translate("Check this order"),
        nzContent: updateForControl,
        nzOkText: 'OK',
        nzOnOk: () => { // chọn ok 
          return new Promise((resolve, reject) => {
            let value: any = {} // giá trị các trường sẽ update
            value['verify_status_received_goods'] = true
            value['verify_date_received_goods'] = this.bill_selected['date_control']
            value['id_verifier_received_goods'] = this.vhAuth.getUser()._id;
            // hàm update
            this.vhQuerySales.updateSalesOrderTiki_byFile([bill.bill_code], value, this.infoShop.access_token, this.infoShop._id).then((bool) => {
              // 

              if (bool[0]) { // nếu update thành công thì gán tên nhân viên khi hover checkbox
                bill['name_received_goods'] = `${this.vhAuth.getlocalEmployee(value['id_verifier_received_goods']).name} ${this.languageService.translate("has checked")} ${this.format_date(value['verify_date_received_goods'])}`
                resolve(true) // ẩn  modal
              } else { this.vhComponent.alertMessageDesktop('warning', this.languageService.translate("An error while updating bill")); resolve(false) }
            },
              error => {
                console.error(error); this.vhComponent.alertMessageDesktop('warning', this.languageService.translate("An error while updating bill")); reject(false)
              }).catch(err => { console.error(err); this.vhComponent.alertMessageDesktop('warning', this.languageService.translate("An error while updating bill")); reject(false) })
          })
        },
        nzOnCancel: () => { bill['verify_status_received_goods'] = false },
        nzCancelText: this.languageService.translate("Cancel")
      })
    } else {
      this.modal.confirm({
        nzTitle: this.languageService.translate("Confirm") + "?",
        nzContent: this.languageService.translate("Cancel check this order"),
        nzOkText: 'OK',
        nzOnOk: () => {
          return new Promise((resolve, reject) => {
            this.vhQuerySales.updateSalesOrderTiki_byFile([bill.bill_code], { verify_status_received_goods: false, id_employee_received_goods: this.vhAuth.getUser()._id }, this.infoShop.access_token, this.infoShop._id).then((bool) => {
              // 
              if (bool[0]) {
                resolve(true)
              } else { this.vhComponent.alertMessageDesktop('warning', this.languageService.translate("An error while updating bill")); resolve(false) }
            },
              error => {
                console.error(error); this.vhComponent.alertMessageDesktop('warning', this.languageService.translate("An error while updating bill")); reject(false)
              }).catch(err => { console.error(err); this.vhComponent.alertMessageDesktop('warning', this.languageService.translate("An error while updating bill")); reject(false) })
          })
        },
        nzOnCancel: () => { bill['verify_status_received_goods'] = true },
        nzCancelText: this.languageService.translate("Cancel")
      })
    }


  }

  goBack() {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/tiki/control/return-control'], {
      state: { dataRestore: { ...this.dataRestore } }
    })
  }

  barcode: string = ''
  barcode_array = [];
  @HostListener("window: keypress", ["$event"])
  /**
   * Sự kiện nhận barcode
   */
  getKeyUp(_event: KeyboardEvent) {
    if (this.router.url === "/sales/dashboard/manage/ecommerce/tiki/control/return-control/return-control-content") {
      // if (!this.showSearchBar && !this.showPayInfo) {
      if (_event.key == "Enter") {
        this.barcode_array.forEach((i, index) => {
          if (i == "Shift") {
            this.barcode_array[index + 1] = this.barcode_array[
              index + 1
            ].toUpperCase();
            this.barcode_array.splice(index, 1);
          }
        });
        // this.scanBarcode(this.barcode_array.join(""));
        this.barcode = this.barcode_array.join("");
        setTimeout(() => {
          this.barcode_array = [];
        }, 100);
        this.search(this.barcode);

      } else {
        this.barcode_array.push(_event.key);
      }
      // }
    }
  }

  startAuto() {
    this.modal.confirm({
      nzTitle: '<i>' + this.languageService.translate('Confirm to control') + '</i>',

      nzOnOk: () => {
        this.vhComponent.showLoading('').then(() => {
          let shop = this.vhQuerySales.getlocalShopListTiki().find(data => data._id == this.infoShop._id)
          if (this.listOrderCode.length)
            this.vhQuerySales.updateSalesOrderTiki_byFile(this.listOrderCode, {
              verify_status_received_goods: true,
              verify_date_received_goods: new Date().toISOString(),
              id_verifier_received_goods: this.vhAuth.getUser()._id,

            }, shop.access_token, shop._id)
              .then((res: any) => {
                
                // 
                if (res.filter(item => !item).length == res.length) this.vhComponent.alertMessageDesktop('warning', this.languageService.translate("Vui lòng xem lại shop đã chọn hoặc file excel!"))
                // xữ lý hiện checkbox  
                else {
                  for (let i in res) {
                    if (!res[i]) this.listData[this.listData.findIndex(item => item.bill_code == this.listOrderCode[i])].verify_status_received_goods = false;
                    else {
                      this.listData[this.listData.findIndex(item => item.bill_code == this.listOrderCode[i])].verify_status_received_goods = true;
                      this.listData[this.listData.findIndex(item => item.bill_code == this.listOrderCode[i])].name_control_goods = `${this.vhAuth.getUser().name} ${this.languageService.translate("has checked")} ${this.format_date(new Date())}`
                    }
                  }
                }
                this.vhComponent.hideLoading(0);
              }, error => {
                this.vhComponent.hideLoading(0)
              })
          else this.vhComponent.hideLoading(0);
        })
      }
    });

  }
}
