import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { IonicModule } from '@ionic/angular';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { TranslateModule } from '@ngx-translate/core'; 
import { RouterModule, Routes } from '@angular/router';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ReturnControlComponent } from './return-control.component';
import { ControlDiaryComponent } from './control-diary/control-diary.component';
import {  ReturnControlContentComponent } from './return-control-content/return-control-content.component';


const routes: Routes = [
  {
    path: '',
    component: ReturnControlComponent,
  },
  {
    path: 'diary-control',
    component: ControlDiaryComponent,
  },
  {
    path: 'return-control-content',
    component: ReturnControlContentComponent,
  },
]
@NgModule({
  declarations: [
    ReturnControlComponent, ControlDiaryComponent ,ReturnControlContentComponent
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    TranslateModule,
    IonicModule,
    FormsModule,
    ReactiveFormsModule
  ]
})
export class ReturnControlModule { }
