
import { Component, OnInit, } from '@angular/core';
import { Router } from '@angular/router';
@Component({
  selector: 'app-return-control',
  templateUrl: './return-control.component.html',
  styleUrls: ['./return-control.component.scss']
})
export class ReturnControlComponent implements OnInit {
  dataRestore: any
  constructor(
    private router: Router
  ) { 
  }

  ngOnInit(): void {
  }

  goBack() {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/tiki/control'])
  }

  gotoMoneyControl() {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/tiki/control/return-control/return-control-content'], { state: { dataRestore: this.dataRestore } })
  }

  gotoDiary() {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/tiki/control/return-control/diary-control'], { state: { dataRestore: this.dataRestore } })
  }
}
